<script>
import Breadcrumbs from "../breadcrumbs/breadcrumbs.vue";
import Page from "../opinion/page.vue";
export default {
  components: { Breadcrumbs, Page },
  data() {
    return {
      status: 0,
      page: 0,
      total_number: 0,
      agent_list: [
        { text: "ÂÖ®ÈÉ®ÂÆòÁΩëÁïôË®Ä‰ø°ÊÅØ", value: 0 },
        { text: "Â∑≤Â§ÑÁêÜ", value: 1 },
        { text: "Êú™Â§ÑÁêÜ", value: -1 }
      ],
      agent_id: { text: "ÂÖ®ÈÉ®", value: 0 },
      items: [
        {
          text: "È¶ñÈ°µ",
          disabled: false,
          to: "/home"
        },
        {
          text: "ÂÆòÁΩëÁî®Êà∑ÁïôË®Ä",
          disabled: false,
          to: "/websiteMessage"
        }
      ],
      data: [],
      description: "",
      dialog: false,
      dialog_: false,
      root_path: this.$cookie.getLocalStorage("root_path").root_path
    };
  },
  mounted() {
    this.info(undefined);
  },
  methods: {
    // ÂàÜÈ°µËß¶Âèë
    info_(e) {
      this.page = e - 1;
      this.info(undefined);
    },
    // Ëé∑ÂèñÂàóË°®
    info(event) {
      // ÈÄâÊã©Ê°ÜËß¶Âèë
      if (event !== undefined) {
        this.page = 0;
        this.status = event;
      }
      this.$axios({
        method: "post",
        url: this.$apis.URL.website_feedback_list,
        data: {
            status: this.status,
            page:this.page
        }
      })
        .then(response => {
          if (response.data.result === 0) {
            this.data = response.data.datas.list;
            this.total_number = response.data.datas.total_number;
          } else {
            this.$Dialog.getProductCheckResult_({text: response.data.message,timeout: 3000, snackbar: true });
          }
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({text: "üò°" + error,timeout: 3000, snackbar: true });
        });
    },
    // Â∑≤Â§ÑÁêÜÁ°ÆËÆ§
    opinion_(id) {
      this.dialog = true;
      this.id = id;
    },
    // Êèê‰∫§ÊåâÈíÆ
    opinion() {
      if (this.description.length < 2) {
        this.$Dialog.getProductCheckResult_({text: "ËØ∑ËæìÂÖ•Ëá≥Â∞ë2‰∏™Â≠óÁ¨¶",timeout: 3000, snackbar: true });
      } else {
        this.$axios({
          method: "post",
          url: this.$apis.URL.website_feedback_edit,
          data: {
            id: this.id,
            status: 1,
            description: this.description
          }
        })
          .then(response => {
            if (response.data.result === 0) {
              this.dialog = false;
              this.id = "";
              this.info(undefined);
              this.$Dialog.getProductCheckResult_({text: "Ê≠§Êù°ÊÑèËßÅÂèçÈ¶àÂ∑≤Ê†áËÆ∞‰∏∫Â∑≤Â§ÑÁêÜÔºÅ",timeout: 3000, snackbar: true });
            } else {
              this.$Dialog.getProductCheckResult_({text: "üò°" + response.data.message,timeout: 3000, snackbar: true });
            }
          })
          .catch(error => {
            this.$Dialog.getProductCheckResult_({text: "üò°" + error,timeout: 3000, snackbar: true });
          });
      }
    },
    // ÂøΩÁï•Á°ÆËÆ§
    ignore_(id) {
      this.dialog_ = true;
      this.id = id;
    },
    // Êèê‰∫§ÂøΩÁï•
    ignore() {
      this.$axios({
        method: "post",
        url: this.$apis.URL.website_feedback_edit,
        data: {
          id: this.id,
          status: 1,
          description: "ÂøΩÁï•"
